import { Component, OnInit } from '@angular/core';
import * as moment from 'moment';
import { HttpTestService } from 'src/app/http-test.service';
import { Router } from '@angular/router';
import { constants } from '../../../constants/constants';
import { HistoricalTabPage } from '../historical-tab/historical-tab.page';
@Component({
  selector: 'app-historical-tab4',
  templateUrl: './historical-tab4.page.html',
  styleUrls: ['./historical-tab4.page.scss'],
})
export class HistoricalTab4Page implements OnInit {

  constructor(public h:HistoricalTabPage,private router: Router, private service: HttpTestService) { }
  header1: string;
  header2: string;
  header3: string;
  keepImageCafe: string;
  keepImageAlmoco: string;
  keepImageJantar: string;
  keepImageCeia: string;
  keepCafe: boolean;
  keepAlmoco: boolean;
  keepJantar: boolean;
  keepCeia: boolean;
  id_cafe: string;
  id_almoco: string;
  id_jantar: string;
  id_ceia: string;
  description_cafe: string;
  description_almoco: string;
  description_jantar: string;
  description_ceia: string;
  date:string;
  ionViewWillEnter(){
    this.keepImageCafe = null
    this.keepImageAlmoco = null
    this.keepImageJantar = null
    this.keepImageCeia = null
    this.keepCafe = null
    this.keepAlmoco = null
    this.keepJantar = null
    this.keepCeia = null
    this.id_cafe = null
    this.id_almoco = null
    this.id_jantar = null
    this.id_ceia = null
    this.description_cafe = null
    this.description_almoco = null
    this.description_jantar = null
    this.description_ceia = null
    window.sessionStorage.setItem('id_meal', "");
    window.sessionStorage.setItem('image_file_name', "");
    window.sessionStorage.setItem('description', "");
    this.header1 = window.sessionStorage.getItem('uid');
    this.header2 = window.sessionStorage.getItem('access-token');
    this.header3 = window.sessionStorage.getItem('client');
    this.date = moment(this.h.initDate).subtract(3, 'days').format('DD/MM/YYYY');
    this.service.getUserData(`api/v1/get-meals/${moment(this.h.initDate).subtract(3, 'days').format('YYYY-MM-DD')}`, this.header1, this.header2, this.header3 ).subscribe(data => {
      console.log("CAFÉ DA MANHÃ É"+JSON.stringify(data.body));
      data.body.forEach(element => {
        if (element.meal_type == "Café da Manhã"){
          this.keepCafe = true;
          this.keepImageCafe = `${constants.baseUrlProd}${element.image_url}`;
          this.id_cafe = element.id;
          this.description_cafe = element.description;
        }else if(element.meal_type == "Almoço" ){
          this.keepAlmoco = true;
          this.keepImageAlmoco = `${constants.baseUrlProd}${element.image_url}`;
          this.id_almoco = element.id;
          this.description_almoco = element.description;
        }else if(element.meal_type == "Janta" ){
          this.keepJantar = true;
          this.keepImageJantar = `${constants.baseUrlProd}${element.image_url}`;
          this.id_jantar = element.id;
          this.description_jantar = element.description;  
        }else if(element.meal_type == "Ceia" ){
          this.keepCeia = true;
          this.keepImageCeia = `${constants.baseUrlProd}${element.image_url}`;
          this.id_ceia = element.id;
          this.description_ceia = element.description;

        }
        
        
      });
      
    })
  }
  
  changePage(id, keepImage, description){
    window.sessionStorage.setItem('id_meal', id);
    window.sessionStorage.setItem('image_file_name', keepImage);
    window.sessionStorage.setItem('description', description);
    this.router.navigateByUrl('user-meal-history');
  }
  ngOnInit() {
  }

}
